import React, { useState } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Alert from 'react-bootstrap/Alert';
import Media from 'react-bootstrap/Media';
import ListGroup from 'react-bootstrap/ListGroup';
import Card from 'react-bootstrap/Card';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import Overlay from 'react-bootstrap/Overlay';
import Tooltip from 'react-bootstrap/Tooltip';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
} from "react-router-dom";

const renderTooltip = (props) => (
  <Tooltip id="button-tooltip" {...props}>
    {props}
  </Tooltip>
);


function MyCard(props){
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = (e) => {
    setShow(true);
    e.preventDefault();
  };


    return(



              <Col xs="12" lg="4">
              <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                  <Modal.Title>{props.title}</Modal.Title>
                </Modal.Header>
                <Modal.Body>{props.content}</Modal.Body>
                <Modal.Footer>
                <OverlayTrigger
                    placement="top"
                    delay={{ show: 250, hide: 400 }}
                    overlay={renderTooltip("Exit")}>
                  <Button variant="danger" onClick={handleClose}>
                    Retour
                  </Button>
                  </OverlayTrigger>
                  <OverlayTrigger
                      placement="top"
                      delay={{ show: 400, hide: 400 }}
                      overlay={renderTooltip("Download this file")}>
                  <Button variant="primary" href={props.fileUrl} download>
                  Télécharger "{props.ty}"
                  </Button>
                  </OverlayTrigger>
                </Modal.Footer>
              </Modal>
              <OverlayTrigger
                  placement="top"
                  delay={{ show: 250, hide: 400 }}
                  overlay={renderTooltip("Appuyez pour télécharger le pdf")}>
              <a href="" onClick={handleShow}>
              <Card className="mb-3 shadow-lg" style={{background: props.color1}}>
                <Card.Body>
                  <Card.Title style={{color:"black"}}>{props.title}</Card.Title>
                  <Card.Subtitle className="mb-2 text-muted">{props.ty}</Card.Subtitle>
                  <Card.Text>
                    {props.content}
                  </Card.Text>
                  <Card.Link >
                    Voire
                  </Card.Link>
                  <Card.Link >{new Date().getMonth()+1}/{new Date().getFullYear()}</Card.Link>
                </Card.Body>
              </Card>
              </a>
              </OverlayTrigger>
              </Col>
    );
}

export default MyCard;
